import React, { useState, useEffect } from "react";

const Days = [
  { key: 0, name: "Sunday", code: "Su" },
  { key: 1, name: "Monday", code: "Mo" },
  { key: 2, name: "Tuesday", code: "Tu" },
  { key: 3, name: "Wednesday", code: "We" },
  { key: 4, name: "Thursday", code: "Th" },
  { key: 5, name: "Friday", code: "Fr" },
  { key: 6, name: "Saturday", code: "Sa" }
];

const Months = [
  { key: 0, name: "January" },
  { key: 1, name: "February" },
  { key: 2, name: "March" },
  { key: 3, name: "April" },
  { key: 4, name: "May" },
  { key: 5, name: "June" },
  { key: 6, name: "July" },
  { key: 7, name: "August" },
  { key: 8, name: "September" },
  { key: 9, name: "October" },
  { key: 10, name: "November" },
  { key: 11, name: "December" }
];

const Calendar = ({ setCheckIn, setCheckOut, visible }) => {
  const [date, setdate] = useState(new Date());
  const [CurrentMonth, setCurrentMonth] = useState({
    startDate: {},
    endDate: {},
    startDay: "",
    endDay: "",
    name: ""
  });
  const [NextMonth, setNextMonth] = useState({
    startDate: 0,
    endDate: 0,
    startDay: "",
    endDay: "",
    name: ""
  });
  const [D, setD] = useState(date.getDay());
  const [M, setM] = useState(date.getMonth());
  const [Y, setY] = useState(date.getFullYear());
  const [CurrentDates, setCurrentDates] = useState([]);
  const [NextDates, setNextDates] = useState([]);

  const display = () => {
    return visible === true ? "block" : "none";
  };

  useEffect(() => {
    getDates();
  }, []);

  const getDates = () => {
    let firstOfCurrentMonth = new Date(Y, M, 1);
    let lastOfCurrentMonth = new Date(Y, M + 1, 0);
    let firstOfNextMonth = new Date(Y, M + 1, 1);
    let lastOfNextMonth = new Date(Y, M + 2, 0);

    let c = { ...CurrentMonth };
    let n = { ...NextMonth };

    c.startDate = firstOfCurrentMonth.getDate();
    c.endDate = lastOfCurrentMonth.getDate();
    c.name = Months[M].name;
    c.startDay = firstOfCurrentMonth.getDay();
    c.endDay = lastOfCurrentMonth.getDay();

    n.startDate = firstOfNextMonth.getDate();
    n.endDate = lastOfNextMonth.getDate();
    n.name = Months[M + 1].name;
    n.startDay = firstOfNextMonth.getDay();
    n.endDay = lastOfNextMonth.getDay();

    setCurrentMonth(c);
    setNextMonth(n);
    resetDates(c, n);
  };

  const RenderDays = Days.map((day, index) => {
    return (
      <div
        className="calendar__day--name"
        key={index}
        data-day__name={day.name}
      >
        {day.code}
      </div>
    );
  });

  const resetDates = (currentMonth, nextMonth) => {
    let currentDates = [];
    let nextDates = [];

    if (currentMonth.startDay > 0) {
      for (let j = 0; j < currentMonth.startDay; j++) {
        currentDates.push(0);
      }
    }
    for (let i = 0; i < currentMonth.endDate; i++) {
      currentDates.push(i + 1);
    }
    if (nextMonth.startDay > 0) {
      for (let j = 0; j < nextMonth.startDay; j++) {
        nextDates.push(0);
      }
    }
    for (let i = 0; i < nextMonth.endDate; i++) {
      nextDates.push(i + 1);
    }

    setCurrentDates(currentDates);
    setNextDates(nextDates);
  };

  const getRange = (start, end, startMonth, endMonth) => {
    startMonth = parseInt(startMonth);
    endMonth = parseInt(endMonth);
    var startDate = parseInt(start.getAttribute("data-date"));
    var endDate = parseInt(end.getAttribute("data-date"));
    if (startMonth === endMonth) {
      for (let i = startDate; i <= endDate; ++i) {
        var elem = document.querySelector(
          `.calendar__date[data-date="${i}"].calendar__date[data-month="${startMonth}"]`
        );
        elem.classList.add("in-range");
      }
    } else {
      var startDays = parseInt(new Date(Y, startMonth + 1, 0).getDate());
      for (let i = startDate; i <= startDays; ++i) {
        var elem = document.querySelector(
          `.calendar__date[data-date="${i}"].calendar__date[data-month="${startMonth}"]`
        );
        elem.classList.add("in-range");
      }

      for (let i = 1; i <= endDate; i++) {
        var elem = document.querySelector(
          `.calendar__date[data-date="${i}"].calendar__date[data-month="${endMonth}"]`
        );
        elem.classList.add("in-range");
      }
    }
  };

  const setSelected = date => {
    var toRemove = document.querySelectorAll(".calendar__date.selected");

    if (toRemove.length > 1) {
      toRemove[0].classList.remove("selected");
      toRemove[0].classList.remove("selected--start");
      toRemove[1].classList.remove("selected");
      toRemove[1].classList.remove("selected--end");
      setCheckIn("Check-in");
      setCheckOut("Check-out");
    }

    var start = document.querySelector(
      ".calendar__date.selected.selected--start"
    );

    var elem = document.querySelector(
      `.calendar__date[data-month="${date.getMonth()}"].calendar__date[data-day="${date.getDay()}"].calendar__date[data-date="${date.getDate()}"].calendar__date[data-year="${date.getFullYear()}"]`
    );

    if (start) {
      elem.classList.add("selected--end");
      setCheckOut(date);
    } else {
      elem.classList.add("selected--start");
      setCheckIn(date);
    }
    elem.classList.add("selected");

    var end = document.querySelector(".calendar__date.selected.selected--end");

    if (start && end) {
      getRange(
        start,
        end,
        start.getAttribute("data-month"),
        end.getAttribute("data-month")
      );
    } else {
      var elems = document.querySelectorAll(".in-range");

      [].forEach.call(elems, function(el) {
        el.classList.remove("in-range");
      });
    }
  };

  const RenderCurrentDates = CurrentDates.map((c, index) => {
    let date = new Date(Y, M, c);

    return (
      <div
        data-month={date.getMonth()}
        data-date={date.getDate()}
        data-day={date.getDay()}
        data-year={date.getFullYear()}
        onClick={setSelected.bind(this, date)}
        key={index}
        className={`
        calendar__date calendar__date--${
          c === 0
            ? "empty"
            : c === D + 1
            ? "today"
            : c < D + 1
            ? "past"
            : "future"
        }
        `}
      >
        {c === 0 ? "" : c}
      </div>
    );
  });

  const RenderNextDates = NextDates.map((c, index) => {
    let date = new Date(Y, M + 1, c);

    return (
      <div
        data-month={M + 1}
        data-date={date.getDate()}
        data-day={date.getDay()}
        data-year={date.getFullYear()}
        onClick={setSelected.bind(this, date)}
        key={index}
        className={`
        calendar__date calendar__date--${c === 0 ? "empty" : "future"}
        `}
      >
        {c === 0 ? "" : c}
      </div>
    );
  });

  return (
    <div style={{ display: `${display()}` }} className="calendar">
      <div className="calendar__control calendar__control--prev"></div>
      <div className="calendar__control calendar__control--next"></div>
      <div className="calendar__content">
        <div className="calendar__wrapper">
          <div className="calendar__month">{CurrentMonth.name}</div>
          <div className="calendar__dates">
            <div className="calendar__head calendar__row">
              <div className="calendar__days">{RenderDays}</div>
            </div>
            <div className="calendar__body">{RenderCurrentDates}</div>
          </div>
        </div>
        <div className="calendar__wrapper">
          <div className="calendar__month">{NextMonth.name}</div>
          <div className="calendar__dates">
            <div className="calendar__head calendar__row">
              <div className="calendar__days">{RenderDays}</div>
            </div>
            <div className="calendar__body">{RenderNextDates}</div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Calendar;
